*/
.cta-btn, .cta-form__btn {
  font-size: 1.5rem;
  text-decoration: none;
  font-family: inherit;
  border: none;
  cursor: pointer;
  border-radius: 1.2rem;
  color: var(--white);
}

.section-cta {
  height: 100vh;
  padding: 0 11.46%;
  font-size: 1.6rem;
  line-height: 2.6rem;
  color: var(--white);
  background-color: var(--primary);
  background-image: url(/images/bg-intro-desktop.png);
  display: flex;
  justify-content: center;
  align-items: center;
}

.cta-container {
  display: grid;
  grid-template-columns: repeat(2, 0.5fr);
  column-gap: 4.5rem;
  background-color: transparent;
}

.cta-text {
  align-self: center;
  justify-self: center;
}
.cta-text__heading {
  font-size: 5rem;
  line-height: 5.5rem;
  font-weight: 700;
  margin-bottom: 1.1rem;
}
.cta-text__description {
  font-weight: 500;
}

.cta {
  border-radius: 1.2rem;
  box-shadow: 0px 8px 0px rgba(0, 0, 0, 0.14688);
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
}
.cta-btn {
  width: 100%;
  padding: 1.7rem 11.1rem;
  background-color: var(--secondary);
  box-shadow: 0px 8px 0px rgba(0, 0, 0, 0.14688);
}
.cta-btn span {
  font-weight: 500;
}
.cta-form {
  padding: 4rem;
  background-color: var(--white);
  border-radius: 1.2rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  font-family: inherit;
}
.cta-form__input {
  padding: 1.5rem 3.2rem 1.5rem 3.2rem;
  border: 1px solid var(--gray);
  border-radius: 0.5rem;
  font-size: 1.4rem;
  font-weight: 500;
  font-family: inherit;
  color: var(--input-text-color);
}
.cta-form__input::placeholder {
  font-size: 1.4rem;
  font-weight: 500;
  color: var(--input-text-color);
}
.cta-form__btn {
  padding: 1.5rem 1.34rem;
  background-color: var(--primary-2);
  font-size: 1.5rem;
  text-transform: uppercase;
  letter-spacing: 0.1rem;
  line-height: 2.6rem;
  font-weight: 600;
}
.cta-form__terms {
  color: var(--gray);
  font-size: 1.1rem;
  text-align: center;
}
.cta-form__terms span {
  color: var(--primary);
  font-weight: 700;
}